import React from 'react'
import PropTypes from 'prop-types'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'
import Img from 'gatsby-image'

const StyledCard = styled.div`
  display: flex;
  flex-direction: column;
  transition: opacity 0.3s ease-in-out;
  margin-bottom: 2rem;

  &:hover {
    opacity: 0.7;

    .post__cta:after {
      width: 100%;
      animation: link-hover 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
    }
  }

  a {
    text-decoration: none;
  }

  h2 {
    margin-bottom: 0;
  }

  .post {
    &__title {
      margin: 0;
    }

    &__meta {
      font-size: 0.7rem;
    }

    &__cta {
      position: relative;
      display: inline-block;
      overflow: hidden;
      vertical-align: middle;
      width: auto;
      margin: 0;
      padding: 0;
      background: transparent;
      border: 0;
      font-size: 0.8em;
      text-align: center;
      cursor: pointer;
      -webkit-appearance: none;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 1px;
        width: 100%;
        background-color: #099fc0;
        -webkit-transition: width 0.3s ease;
        transition: width 0.3s ease;
      }
    }
  }
`

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: black;
`

const StyledImg = styled(Img)`
  display: none;
  margin-bottom: 1rem;

  @media (max-width: 768px) {
    display: block;
  }
`

const PostCard = ({ post, handleImageChange, idx }) => {
  const url = `/blog/${post.slug}/`
  const { created_at_pretty, title, localFeatureImage } = post

  return (
    <StyledCard onMouseOver={() => handleImageChange(idx)}>
      <article>
        <StyledLink
          cover
          bg={'black'}
          direction="right"
          to={url}
          className="post-card"
        >
          <StyledImg fluid={localFeatureImage.childImageSharp.fluid} />
          <span className="post__meta">{created_at_pretty}</span>
          <h2 className="post__title">{title}</h2>
          <span className="post__cta">Read More</span>
        </StyledLink>
      </article>
    </StyledCard>
  )
}

PostCard.propTypes = {
  post: PropTypes.shape({
    slug: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    feature_image: PropTypes.string,
    featured: PropTypes.bool,
    tags: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string,
      }),
    ),
    excerpt: PropTypes.string.isRequired,
    primary_author: PropTypes.shape({
      name: PropTypes.string.isRequired,
      profile_image: PropTypes.string,
    }).isRequired,
  }).isRequired,
}

export default PostCard
