import React from 'react'
import Img from 'gatsby-image'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: black;
`

const StyledContainer = styled.div`
  display: flex;
  flex-direction: row;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  margin-top: 4rem;
  padding: 4rem 0;

  &:hover {
    figure {
      transform: scale(1.2);
    }
    .scaler {
      transform: scale(0.98);
    }

    .next-post-cta:after {
      width: 100%;
      animation: link-hover 0.8s cubic-bezier(0.58, 0.3, 0.005, 1) 0s 1;
    }
  }

  figure {
    transition: 0.3s ease-in-out;
    margin: 0;
  }

  & > * {
    flex-basis: 50%;
  }

  .scaler {
    transition: transform 0.3s ease-in-out;
    overflow: hidden;
  }

  .next-post-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2rem;

    .content-title {
      margin: .5rem 0;
      line-height: 1.2;
    }
  }

  .next-post-cta {
    align-self: flex-start;
    position: relative;
    background: transparent;
    overflow: hidden;
    border: 0;
    font-size: 0.8em;
    text-align: center;
    cursor: pointer;
    -webkit-appearance: none;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      width: 100%;
      background-color: #099fc0;
      -webkit-transition: width 0.3s ease;
      transition: width 0.3s ease;
    }
`

const NextPost = ({ next }) => {
  const url = `/blog/${next.slug}`

  return (
    <StyledLink
      to={url}
      cover
      bg={'black'}
      direction="right"
      className="post-card"
    >
      <StyledContainer>
        <div className="next-post-left">
          {next.feature_image ? (
            <div className="scaler">
              <ScrollAnimation animateIn="scale-down" delay={150}>
                <figure className="post-feature-image">
                  <Img
                    fluid={next.localFeatureImage.childImageSharp.fluid}
                    alt={next.title}
                  />
                </figure>
              </ScrollAnimation>
            </div>
          ) : null}
        </div>
        <div className="next-post-right">
          <ScrollAnimation animateIn="fadeInUp" delay={300}>
            <span>Up next</span>
          </ScrollAnimation>
          <ScrollAnimation animateIn="fadeInUp" delay={450}>
            <h2 className="content-title">{next.title}</h2>
          </ScrollAnimation>
          <ScrollAnimation
            style={{ overflow: 'hidden', width: 'fit-content' }}
            animateIn="fadeInUp"
            delay={600}
          >
            <span className="next-post-cta">Read more</span>
          </ScrollAnimation>
        </div>
      </StyledContainer>
    </StyledLink>
  )
}

export default NextPost
